body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home CSS */
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #1c2237;
}

.description {
  font-size: 24px;
  margin-bottom: 40px;
  color: #58616f;
}

.signup-btn {
  background-color: #0984e3;
  color: #fff;
  font-size: 18px;
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.signup-btn:hover {
  background-color: #74b9ff;
}

/* Navbar CSS */
nav {
  background-color: white;
  height: 75px; /* reduce the height of the navbar */
  width: 100%; /* make the width of the navbar 100% of the page */
  position: fixed; /* make the navbar fixed so that it stays on top */
  top: 0;
  z-index: 3;
  
}

a.home-link.icon, a.login-link.icon, a.contact-link.icon {
  color: black;
}

.icon {
  height: 30px;
  color: white;
  opacity: none;
}

nav {
  display: block;
  text-align: center;
  position: relative; /* change the position to relative */
  margin: 0 auto; /* center the navbar horizontally */
  max-width: 100%; /* add a max-width to the navbar */
  height: 90px;
}

nav a {
  font-size: 22px;
  color: #4d4d4e;
  display: inline-block; /* change display to inline-block */
  line-height: 90px; /* change line-height to match the height of the navbar */
  padding: 0 20px; 
  position: relative;
  left: 450px;
}

nav a i {
  transition: none;
}

a:hover {
  color: rgb(221, 221, 212);
}

/* nav a:hover svg {
  opacity: 0;
} */

nav a:hover:after {
  opacity: 1;
}



a:after {
  content: ' ';
  font-size: 12px;
  letter-spacing: 2px;
  position: relative;
  bottom: 0;
  display: block;
  width: 50%;
  text-align: center;
  opacity: 0;
  transition: all 0.3s ease-out;
}

