/* Sign up CSS */
.SignUpContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 200px;
  margin-left: 50px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 500px;
}

label {
  margin-top: 20px;
}

input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 100%;
  max-width: 400px;
  margin-top: 5px;
}

button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #3f51b5;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/* Contact Info CSS */
.contact-form {
  background-color: aqua;
  left: 70px;
}

